.home--logged--out--main {
  padding: 3rem 1rem;
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  justify-items: flex-start;
  grid-gap: 15px 0;
  position: relative;
}
.home__svg__container {
  grid-area: 1/1/span 1/span 1;
}
.home__svg__container img {
  width: 100%;
  height: 25rem;
  display: block;
}
.home__content__container h1 {
  line-height: 2.5rem;
  font-family: var(--secondary-font);
  font-size: 3rem;
  color: var(--h1-color);
  font-weight: 500;
}
.home__content__container p {
  margin: 0.35rem 0 0.5rem;
  color: #888;
  font-size: 0.9rem;
}

.get__started__link {
  font-family: var(--tertiary-font);
  background-color: var(--btn-color);
  text-transform: uppercase;
  border: none;
  font-weight: 500;
  display: inline-block;
  text-decoration: none;
  padding: 0.75rem 1.25rem;
  margin-top: 0.4rem;
  border-radius: 4px;
  color: var(--color-white);
  letter-spacing: 0.5px;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.2), 2px 2px 2px var(--shadow-color);
  transition: transform 0.2s cubic-bezier(0.18, 0.89, 0.32, 1.28), box-shadow 0.2s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}
.get__started__link i {
  margin-left: 0.5rem;
  font-size: 1.1rem;
}
.get__started__link:hover, .get__started__link:focus {
  transform: translateY(-3px);
  box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.6);
}
.get__started__link:active {
  transform: translateY(-1px);
  box-shadow: 3px 3px 5px var(--shadow-color);
}

.home--logged--out--main::after, .home--logged--out--main::before {
  position: absolute;
  width: 15rem;
  height: 15rem;
  background-image: linear-gradient(#fff8ea, #fbe6ff, #ffd5cc);
  border-radius: 40% 50% 35% 45%/35% 45% 30% 50%;
  z-index: -1;
}
.home--logged--out--main::after {
  left: 50px;
  top: 50px;
  animation: animate-pseudoAfter 30s ease infinite;
  animation-direction: reverse;
}
.home--logged--out--main::before {
  right: 50px;
  top: 100px;
  animation: animate-pseudoBefore 45s ease infinite;
  animation-direction: reverse;
}

@keyframes animate-pseudoAfter {
  25% {
    transform: translate(200px, 100px) scale(0.9);
    border-radius: 50% 30% 45% 40%/35% 40% 35% 40%;
  }
  50% {
    transform: translate(350px, 250px) scale(1);
    border-radius: 40% 40% 39% 34%/38% 40% 36% 45%;
  }
  75% {
    transform: translate(500px 400px) scale(1.1);
    border-radius: 50% 46% 30% 30%/48% 60% 46% 35%;
  }
}
@keyframes animate-pseudoBefore {
  25% {
    transform: translate(-200px, 100px) scale(0.8);
    border-radius: 50% 30% 45% 40%/35% 40% 35% 40%;
  }
  50% {
    transform: translate(-300px, 150px) scale(1);
    border-radius: 40% 40% 39% 34%/38% 40% 36% 45%;
  }
  75% {
    transform: translate(-500px 350px) scale(1.2);
    border-radius: 50% 46% 30% 30%/48% 60% 46% 35%;
  }
}
@media screen and (min-width: 761px) {
  .home--logged--out--main {
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }
  .home--logged--out--main::after, .home--logged--out--main::before {
    content: "";
  }

  .home__svg__container {
    grid-area: 1/2/span 1/span 1;
  }
}